import { getConfig } from './config';
import { debug, DiagnosticSeverity, env, languages, window, workspace } from 'vscode';
import { DEBUGGING_IMAGE_KEY, FAKE_EMPTY, IDLE_VSCODE_IMAGE_KEY, IDLE_VSCODE_INSIDERS_IMAGE_KEY, VSCODE_IMAGE_KEY, VSCODE_INSIDERS_IMAGE_KEY } from './constants';
import { getGitRepo, resolveFileIcon, toLower, toTitle, toUpper } from './utils';
import { basename, parse, sep } from 'path';
let isViewing = false;
let totalProblems = 0;
export function toggleViewing(viewing) {
    isViewing = viewing;
}
export function onDiagnosticsChange() {
    const diag = languages.getDiagnostics();
    let counted = 0;
    diag.forEach((i) => {
        if (i[1]) {
            i[1].forEach((i) => {
                if (i.severity === DiagnosticSeverity.Warning ||
                    i.severity === DiagnosticSeverity.Error) {
                    counted++;
                }
            });
        }
    });
    totalProblems = counted;
}
export async function activity(previous = {}) {
    const config = getConfig();
    const { appName } = env;
    const insiders = appName.includes('Insiders');
    const defaultSmallImageKey = debug.activeDebugSession
        ? DEBUGGING_IMAGE_KEY
        : insiders
            ? VSCODE_INSIDERS_IMAGE_KEY
            : VSCODE_IMAGE_KEY;
    const defaultSmallImageText = config["smallImage" /* SmallImage */].replace("{app_name}" /* AppName */, appName);
    const defaultLargeImageText = config["largeImageIdling" /* LargeImageIdling */];
    let presence = {
        details: details("detailsIdling" /* DetailsIdling */, "detailsViewing" /* DetailsViewing */, "detailsEditing" /* DetailsEditing */, "detailsDebugging" /* DetailsDebugging */),
        startTimestamp: config["removeElapsedTime" /* RemoveElapsedTime */]
            ? undefined
            : previous.startTimestamp ?? Date.now(),
        largeImageKey: insiders
            ? IDLE_VSCODE_IMAGE_KEY
            : IDLE_VSCODE_INSIDERS_IMAGE_KEY,
        largeImageText: defaultLargeImageText,
        smallImageKey: defaultSmallImageKey,
        smallImageText: defaultSmallImageText
    };
    if (window.activeTextEditor) {
        const largeImageKey = resolveFileIcon(window.activeTextEditor.document);
        const largeImageText = config["largeImage" /* LargeImage */]
            .replace("{lang}" /* LanguageLowerCase */, toLower(largeImageKey))
            .replace("{Lang}" /* LanguageTitleCase */, toTitle(largeImageKey))
            .replace("{LANG}" /* LanguageUpperCase */, toUpper(largeImageKey))
            .padEnd(2, FAKE_EMPTY);
        presence = {
            ...presence,
            details: details("detailsIdling" /* DetailsIdling */, "detailsViewing" /* DetailsViewing */, "detailsEditing" /* DetailsEditing */, "detailsDebugging" /* DetailsDebugging */),
            state: details("lowerDetailsIdling" /* LowerDetailsIdling */, "lowerDetailsViewing" /* LowerDetailsViewing */, "lowerDetailsEditing" /* LowerDetailsEditing */, "lowerDetailsDebugging" /* LowerDetailsDebugging */),
            largeImageKey,
            largeImageText
        };
        if (config["buttonEnabled" /* ButtonEnabled */]) {
            const gitRepo = await getGitRepo(window.activeTextEditor.document.fileName);
            if (gitRepo && config["buttonActiveLabel" /* ButtonActiveLabel */]) {
                presence = {
                    ...presence,
                    buttons: [
                        {
                            label: config["buttonActiveLabel" /* ButtonActiveLabel */],
                            url: gitRepo
                        }
                    ]
                };
            }
            else if (!gitRepo &&
                config["buttonInactiveLabel" /* ButtonInactiveLabel */] &&
                config["buttonInactiveUrl" /* ButtonInactiveUrl */]) {
                presence = {
                    ...presence,
                    buttons: [
                        {
                            label: config["buttonInactiveLabel" /* ButtonInactiveLabel */],
                            url: config["buttonInactiveUrl" /* ButtonInactiveUrl */]
                        }
                    ]
                };
            }
        }
    }
    return presence;
}
function details(idling, viewing, editing, debugging) {
    const config = getConfig();
    let raw = config[idling].replace("{empty}" /* Empty */, FAKE_EMPTY);
    if (window.activeTextEditor) {
        const fileName = basename(window.activeTextEditor.document.fileName);
        const { dir } = parse(window.activeTextEditor.document.fileName);
        const split = dir.split(sep);
        const dirName = split[split.length - 1];
        const noWorkspaceFound = config["lowerDetailsNoWorkspaceFound" /* LowerDetailsNoWorkspaceFound */].replace("{empty}" /* Empty */, FAKE_EMPTY);
        const workspaceFolder = workspace.getWorkspaceFolder(window.activeTextEditor.document.uri);
        const workspaceFolderName = workspaceFolder?.name ?? noWorkspaceFound;
        const workspaceName = workspace.name ?? workspaceFolderName;
        const workspaceAndFolder = `${workspaceName}${workspaceFolderName === FAKE_EMPTY ? '' : ` - ${workspaceFolderName}`}`;
        const fileIcon = resolveFileIcon(window.activeTextEditor.document);
        const problems = config["showProblems" /* ShowProblems */]
            ? config["problemsText" /* ProblemsText */].replace("{problemsCount}" /* ProblemsCount */, totalProblems.toString())
            : '';
        raw = config[debug.activeDebugSession ? debugging : isViewing ? viewing : editing];
        if (workspaceFolder) {
            const { name } = workspaceFolder;
            const relativePath = workspace
                .asRelativePath(window.activeTextEditor.document.fileName)
                .split(sep);
            relativePath.splice(-1, 1);
            raw = raw.replace("{full_dir_name}" /* FullDirName */, `${name}${sep}${relativePath.join(sep)}`);
        }
        raw = fileDetails(raw, window.activeTextEditor.document, window.activeTextEditor.selection);
        raw = raw
            .replace("{file_name}" /* FileName */, fileName)
            .replace("{dir_name}" /* DirName */, dirName)
            .replace("{workspace}" /* Workspace */, workspaceName)
            .replace("{workspace_folder}" /* WorkspaceFolder */, workspaceFolderName)
            .replace("{workspace_and_folder}" /* WorkspaceAndFolder */, workspaceAndFolder)
            .replace("{lang}" /* LanguageLowerCase */, toLower(fileIcon))
            .replace("{Lang}" /* LanguageTitleCase */, toTitle(fileIcon))
            .replace("{LANG}" /* LanguageUpperCase */, toUpper(fileIcon))
            .replace("{problems}" /* Problems */, problems);
    }
    return raw;
}
function fileDetails(_raw, document, selection) {
    let raw = _raw.slice();
    if (raw.includes("{total_lines}" /* TotalLines */)) {
        raw = raw.replace("{total_lines}" /* TotalLines */, document.lineCount.toLocaleString());
    }
    if (raw.includes("{current_line}" /* CurrentLine */)) {
        raw = raw.replace("{current_line}" /* CurrentLine */, (selection.active.line + 1).toLocaleString());
    }
    if (raw.includes("{current_column}" /* CurrentColumn */)) {
        raw = raw.replace("{current_column}" /* CurrentColumn */, (selection.active.character + 1).toLocaleString());
    }
    return raw;
}
